.todoList {
    padding: 10px;
    width: 350px;
    margin: 10px auto;
    background-color: #555;
    border-radius: 10px;
    box-shadow: 0px 4px 8px rgba(27, 27, 28, 0.91);
}

.MainComponentTodoList {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: flex-start;
}

.center {
    text-align: center;
}